/**
 * Dep
 * key sub
 * Dependencekey
 * Subscription
 * 
 */
class Dep {
    constructor () {
        this.subs = new Set();
    }
    addSub (sub) {// Set
        this.subs.add(sub);
    }
    removeSub (sub) {// 
        this.subs.delete(sub);
    }
    notify () {// update
        for (let sub of this.subs) {
            sub.update();
        }
    }
}
Dep.target = null;

export default Dep;