import Dep from '../dependence';

// Observer
/**
 * data
 * @param {*} data 
 */
function observe (data) {
    if (Object.prototype.toString.call(data) === '[object Object]') {//object
        for (let prop in data) {
            defineReactive(data, prop, data[prop]);
        }
    }
}
/**
 * key
 * @param {*} obj 
 * @param {*} key 
 * @param {*} val 
 */
function defineReactive (obj, key, val) {
    const dep = new Dep();// keykey
    Reflect.defineProperty(obj, key, {
        enumerable: true,
        configurable: true,
        get () {
            // (watcher)
            // targetWatcherWatcherDep
            // DepaddSubSetWatcher
            Dep.target && dep.addSub(Dep.target);
            return val;// 
        },
        set (newVal) {
            // 
            if (newVal === val) return;//
            val = newVal;//
            observe(newVal);//
            dep.notify();// key
        }
    });
    observe(val);//
}

export default observe;