import walkChildren from './compiler'
import observe from './observer'

// Vue
/**
 * 
 * optionsdatacomputedmethodsVue
 * @param {*} options 
 */
// 
function proxy (options) {
    for (let prop in options.data) {
        Reflect.defineProperty(this, prop, {
            enumerable: true,
            configurable: true,
            get () {
                return this.$options.data[prop];
            },
            set (newVal) {
                this.$options.data[prop] = newVal;
            }
        })
    }
}
/**
 * (ObserverCompilerDepWatcher)
 * Vueobserverdatacompileelcompilewatchereldata
 * ObservermodelCompilevue {{}}watcherobserverCompile
 *  >input>model
 */
window.Vue = class Vue {
    constructor (options) {
        this.$data = options.data;
        this.$el = document.querySelector(options.el);
        observe(this.$data);
        proxy(this, options);
        walkChildren(this.$el, this.$data);
    }
}