import Dep from '../dependence'
import { expToFunc } from '../util';
// Watcher
/**
 * Watcher
 * dataCompilerDOM
 * expscopeDOMcallback
 */
class Watcher {
    constructor (exp, scope, callback) {
        this.value = null;// 
        this.getValue = expToFunc(exp, scope);// ,$datavalue
        this.callback = callback;
        this.update();// 
    }
    get () {
        Dep.target = this;// targetWatcher
        let value = this.getValue();// keygetter
        Dep.target = null;// target
        return value;
    }
    update () {
        let newVal = this.get();// 
        if (this.value !== newVal) {// callbackDOM
            this.value = newVal;
            this.callback && this.callback(newVal);
        }
    }
}

export default Watcher;